import React, { useRef, useState, useEffect, useContext } from 'react'
import Tab from '@mui/material/Tab'
import TabContext from '@mui/lab/TabContext'
import TabList from '@mui/lab/TabList'
import TabPanel from '@mui/lab/TabPanel'
import Box from '@mui/material/Box'
import FormControl from '@mui/material/FormControl'
import LockOutlinedIcon from '@mui/icons-material/LockOutlined'
import Visibility from '@mui/icons-material/Visibility'
import VisibilityOff from '@mui/icons-material/VisibilityOff'
import InputAdornment from '@mui/material/InputAdornment'
import IconButton from '@mui/material/IconButton'
import EmailOutlinedIcon from '@mui/icons-material/EmailOutlined'
import ArrowForwardIcon from '@mui/icons-material/ArrowForward'
import Input from '@mui/material/Input'
import Button from '@mui/material/Button'
import AuthContext from '../../context/AuthProvider'
import axios from '../../api/axios'
import './Login.scss'

const LOGIN_URL = '/auth'

function Login() {
  const { setAuth } = useContext(AuthContext)
  const userRef = useRef()
  const errRef = useRef()

  const [user, setUser] = useState('')
  const [pwd, setPwd] = useState('')
  const [errMsg, setErrMsg] = useState('')
  const [success, setSuccess] = useState(false)
  const [value, setValue] = React.useState('1')
  const [values, setValues] = React.useState({
    email: '',
    password: '',
    showPassword: false,
  })

  useEffect(() => {
    setErrMsg('')
  }, [user, pwd])

  const handleChange = (event, newValue) => {
    setValue(newValue)
  }
  const handleFieldChange = (prop) => (event) => {
    setValues({ ...values, [prop]: event.target.value })
  }
  const handleSubmit = async (e) => {
    e.preventDefault()

    try {
      const response = await axios.post(
        LOGIN_URL,
        JSON.stringify({ user, pwd }),
        {
          headers: { 'Content-Type': 'application/json' },
          withCredentials: true,
        }
      )
      console.log(JSON.stringify(response?.data))
      // console.log(JSON.stringify(response));
      const accessToken = response?.data?.accessToken
      const roles = response?.data?.roles
      setAuth({
        user,
        pwd,
        roles,
        accessToken,
      })
      setUser('')
      setPwd('')
      setSuccess(true)
    } catch (err) {
      if (!err?.response) {
        setErrMsg('No Server Response')
      } else if (err.response?.status === 400) {
        setErrMsg('Missing Username or Password')
      } else if (err.response?.status === 401) {
        setErrMsg('Unauthorized')
      } else {
        setErrMsg('Login Failed')
      }
      errRef.current.focus()
    }
  }
  const handleClickShowPassword = () => {
    setValues({
      ...values,
      showPassword: !values.showPassword,
    })
  }

  const handleMouseDownPassword = (event) => {
    event.preventDefault()
  }

  return (
    <div className="login-p">
      <img
        className="logo"
        src={`${process.env.PUBLIC_URL}/images/logo.svg`}
        alt=""
      />
      <Box className="section-auth">
        <TabContext value={value}>
          <TabList onChange={handleChange} aria-label="authentication yeap">
            <Tab label="LOGIN" value="1" />
            <Tab label="SIGN UP" value="2" />
          </TabList>
          <TabPanel value="1">
            <form onSubmit={handleSubmit}>
              <Input
                value={values.email}
                onChange={handleFieldChange('email')}
                placeholder="Email"
                startAdornment={
                  <InputAdornment position="start">
                    <EmailOutlinedIcon sx={{ color: '#888888' }} />
                  </InputAdornment>
                }
              />

              <Input
                type={values.showPassword ? 'text' : 'password'}
                value={values.password}
                onChange={handleFieldChange('password')}
                placeholder="Email password"
                startAdornment={
                  <InputAdornment position="start">
                    <LockOutlinedIcon sx={{ color: '#888888' }} />
                  </InputAdornment>
                }
                endAdornment={
                  <InputAdornment position="end">
                    <IconButton
                      aria-label="toggle password visibility"
                      onClick={handleClickShowPassword}
                      onMouseDown={handleMouseDownPassword}
                    >
                      {values.showPassword ? <VisibilityOff /> : <Visibility />}
                    </IconButton>
                  </InputAdornment>
                }
              />
              <Button
                className="login-btn"
                variant="contained"
                type="submit"
                endIcon={<ArrowForwardIcon />}
              >
                LOGIN
              </Button>
              <Button className="forgot-pwd-btn" size="medium">
                Forgot Password
              </Button>
            </form>
          </TabPanel>
          <TabPanel value="2">Item Two</TabPanel>
        </TabContext>
      </Box>
      <div className="license">
        © 2021 YEAP TRANSPORT PTE LTD. ALL RIGHTS RESERVED
      </div>
    </div>
  )
}

export default Login
